import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'app-split',
    template: './split.component.html',
    styleUrls: ['./split.component.css']
})
export class SplitComponent implements OnInit {

    /**  row() | column() */
    @Input() type: 'row'| 'column' = 'row';

    /**  22 */
    @Input() amount = 2;



    constructor() { }

    ngOnInit() {
    }

}



