import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs/Subject';
import { FileTreeDataAdapter } from './FileTreeDataAdapter';
import { TreeNodeData } from '../models/tree-node-data';
import { SERVER_RESPONSE_RESULT } from './mockData';
import { environment } from '../../../../environments/environment';
import { Observable } from 'rxjs';
import { EventBus } from '../../../event';

// url
// url
// url
// 
// access environment.SERVER_IP
const SERVER_IP: string = environment.SERVER_IP;
const PROJECTS_DIRECTORY: string = '/api/dev/main/v1.0/project';
const TREE_DIRECTORY: string = '/tree';
const FILE_DIRECTORY: string = '/file';

@Injectable()
export class FileTreeDataService {
  // TypeScript 
  // TypeScript's constructor syntax for declaring parameters and properties simultaneously.
  // Http
  constructor(private http: HttpClient) {

  }

  /**
   * 
   * @param parentNodePath 
   * @param depth  1
   * @param includeFiles false
   */
  public getChildrenData(parentNodePath: string = '', depth: number = 1, includeFiles: boolean = false): Subject<TreeNodeData[]>{

    let http = this.http;
    let url = this.getChildrenDataUrl(parentNodePath, depth, includeFiles);

    let treeNodesDataSubject = new Subject<TreeNodeData[]>();
    http.get(url).subscribe(
      result => {
        // 
        let fileTreeDataAdapter: FileTreeDataAdapter = new FileTreeDataAdapter(result);

        let treeNodeData: TreeNodeData = fileTreeDataAdapter.getAdaptedServerData();
        console.log("treeNodeData" + treeNodeData);

        if(treeNodeData != null){
          treeNodesDataSubject.next(treeNodeData.children);
        }

      });
    return treeNodesDataSubject;

  }

  /**
   * mock
   */
  public getChildrenDataWithMock(): TreeNodeData[]{
    // Mock
    let result = SERVER_RESPONSE_RESULT;
    let treeNodesData: TreeNodeData[] = [];

    // 
    let fileTreeDataAdapter: FileTreeDataAdapter = new FileTreeDataAdapter(result);

    let treeNodeData: any = fileTreeDataAdapter.getAdaptedServerData();
    console.log("treeNodeData" + treeNodeData);

    // 
    if(treeNodesData != null && treeNodeData != null){
      treeNodesData = treeNodeData.children;
    }

    return treeNodesData;
  }

  public addFileChild(parentNodePath: string, addedFileName: string): Observable<any>{
    let http = this.http;
    let url = this.addFileDataUrl(parentNodePath, addedFileName);

    return http.post<any>(url, null);
  }

  public removeFileChild(parentNodePath: string, removedFileName: string): Observable<any>{
    let http = this.http;
    let url = this.removeFileDataUrl(parentNodePath, removedFileName);

    return http.delete(url, {});
  }

  public addFile(){

  }

  public updateFile(){

  }

  public addFolder(){

  }

  public addProject(){

  }

  // 10.24.19.140:5000/api/dev/main/v1.0/project/{parentwspath}
  private removeFileDataUrl(parentNodePath: string, sourceFileName: string): string{
    let url: string = this.getRootNodeUrl();
    // parentNodePathfileProject
    if(parentNodePath == null || parentNodePath.length == 0 || sourceFileName.length == 0){
      return null;
    }
    url += parentNodePath +'/' + sourceFileName;
    return url;
  }

  // 10.24.19.140:5000/api/dev/main/v1.0/project/file/{parentwspath}?name=fileName
  private addFileDataUrl(parentNodePath: string, sourceFileName: string): string{
    let url: string = this.getRootNodeUrl() + FILE_DIRECTORY;
    // parentNodePathfileProject
    if(parentNodePath == null || parentNodePath.length == 0){
      return null;
    }
    url += parentNodePath;
    url += '?name=' + sourceFileName;
    return url;
  }

  /**
   * url
   * @param parentNodePath 
   * @param depth  1
   * @param includeFiles false
   */
  private getChildrenDataUrl(parentNodePath: string, depth: number = 1, includeFiles:boolean = false): string{
    let url: string = this.getRootNodeUrl() + TREE_DIRECTORY;
    
    // parentNodeNamejs
    if(parentNodePath.length != 0){
      url += parentNodePath;
    }

    url += '?depth=' + depth + '&includeFiles=' + includeFiles;

    return url;
  }

  /**
   * url
   */
  private getRootNodeUrl(): string{
    return SERVER_IP + PROJECTS_DIRECTORY;
  }

}



